<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('PlanHistory') }}</h4>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div>
                            <div class="heading-border">
                                <h5 class="h5">
                                    <span>{{ $t('History') }}</span>
                                </h5>
                            </div>

                            <div class="listing mt-2">
                                <!-- <div class="row">
                                    <div class="col-12 mb-2">
                                        <div class="mr-2 d-inline-block">
                                            <span class="legend-Progress colorboxsquare"></span>
                                            {{ $t('CurrentVersion') }}
                                        </div>
                                    </div>
                                </div> -->

                                <div class="table-responsive">
                                    <table id="tbllistinghistorydata"
                                        class="table table-bordered dt-responsive nowrap mt-0 table_w800">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>{{ $t('PlanName') }}</th>
                                                <th>{{ $t('InvoiceTemplate') }}</th>
                                                <th>{{ $t('EffectiveDate') }}</th>
                                                <th>{{ $t('ExpirationDate') }}</th>
                                                <th>{{ $t('CreatedDate') }}</th>
                                                <th>{{ $t('CreatedBy') }}</th>
                                                <th>{{ $t('BillingFrequency') }}</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(data, index) in PlanHistoryList" :key="index"
                                                :class="{ 'low-bar': data.VERSION_NAME === 'CURRENT' }">
                                                <td>{{ data.PLAN_NAME }}</td>
                                                <td>{{ data.INVOICE_TEMPLATE }}</td>
                                                <td>{{ data.EFFECTIVE_DATE != null ? $d(data.EFFECTIVE_DATE, "long") :
                                                    '' }}
                                                </td>
                                                <td>{{ data.EXPIRATION_DATE != null ? $d(data.EXPIRATION_DATE) : '' }}
                                                </td>
                                                <td>{{ $d(data.CREATE_AT,) }}</td>
                                                <td>{{ data.CREATE_BY }}</td>
                                                <td>{{ data.BILLING_FREQUENCY }}</td>
                                            </tr>
                                            <tr v-if="PlanHistoryList.length === 0">
                                                <td colspan="8" class="text-danger text-center no-record">
                                                    {{ $t('NoRecordfound') }}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from "@/services/DataService";

export default {
    props: {
        planId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            PlanHistoryList: [],
            isLoading: false
        }
    },
    created() {
        this.getPlanHistory();
    },
    methods: {
        getPlanHistory() {
            this.isLoading = true;
            const url = `planId=${this.planId}`;
            DataService.GetPlanHistoryListing(url)
                .then((response) => {
                    debugger
                    if (response && response.data) {
                        response.data.forEach(element => {
                            ['EFFECTIVE_DATE', 'EXPIRATION_DATE', 'CREATED_AT'].forEach((key) => {
                                if (element[key]) {
                                    element[key] = new Date(element[key]);
                                }
                            });
                        });
                        this.PlanHistoryList = response.data;
                        this.isLoading = false;
                    }
                })
                .catch((error) => {
                    this.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                });
        },
        Close() {
            this.$parent.isPlanPopup = false;
        }
    }
}
</script>


<style lang="scss" scoped></style>