<template>
    <section>
        <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Billing" :listType="['List']"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" SubModuleCode="Billing"
            IdentityColumn="id" :HeaderText="$t('Plans')" :ListData="PlanListing" :HeaderData="Headers"
            :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :LegendArray="[{
                class: 'legends_profile colorboxsquare',
                text: $t('Expired')
            }
            ]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">

                <tr v-bind:class="data.row.additionalClass">
                    <!-- your <td> columns here -->
                </tr>
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <listing-status :isfor="'PLAN'" ref="updatestatus" v-bind:dataid="data.row.id" data-controller="Crm"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id" data-action="index"
                        v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true" v-bind:title="data.row.status_id == 1001 ? 'Active' : 'Inactive'">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'STATUS_ID')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'plan_name'">
                    <!-- <RouterLink :to="data.row.plan_code === 'SUBSCRIPTION_BILLING' ? { name: 'ManageSubscriptionPlanDetail', params: { id: data.row.id } } : { name: 'ManagePlan', params: { id: data.row.id } }">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink> -->
                        <!-- <RouterLink 
                        :to="'/Plan/ManageSubscriptionPlanDetail/' + data.row.id" :title="data.row[data.column.COLUMN_NAME]"
                        data-toggle-tooltip="tooltip" data-original-title="Click here to Update Account">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink> -->

                    <a href="javascript:;" v-on:click="OpenPlanDetail(data.row.id, data.row.plan_code)"><i
                            aria-hidden="true"></i>{{
                                data.row.plan_name }}
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'plan_image_url'">
                    <div class="form-control p-0 border-0" v-if="data.row.plan_image_url != null" alt="" title="">
                        <img :src='data.row.plan_image_url' class="border p-2" height="50" width="100" title="" />
                    </div>
                    <div class="form-control p-0 border-0" v-if="data.row.plan_image_url == null">
                        <img src='/Content/images/imagenotavailable.jpg' alt="No Image Available" height="50"
                            width="100" title="" />
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'effective_date'">
                    <span>
                        {{
                            data.row.effective_date != null ? $d(data.row.effective_date) : ''
                        }}
                        <!-- {{ $options.filters.formatDate(data.row.effective_date) }} -->
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'expiration_date'">
                    <span>
                        {{ data.row.expiration_date != null ? $d(data.row.expiration_date) : '' }}
                        <!-- {{ $options.filters.formatDate(data.row.expiration_date) }} -->
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <span>
                        {{ $d(data.row.created_at) }}
                        <!-- {{ $options.filters.formatDate(data.row.created_at) }} -->
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'client_count'">
                    <span>
                        <a id="ancProjectDetail"
                            :class="{ 'text-dark text-center single-action': true, 'disabled': (data.row.client_count == 0) }"
                            href="javascript:;" v-on:click="OpenClientListPopup(data.row.id, data.row.plan_code)">
                            <em class="fa fa-user" aria-hidden="true"></em>
                            <span class="noti-circle noti-blue">{{ data.row.client_count }}</span>
                        </a>
                    </span>
                </template>
            </template>
        </tg-list>
        <AddPlan v-if="isEditPlan" :callbackfunction="FetchData" :billingTypeOptions="BillingTypeOptions"
            :editDetail="EditPlanDetail" :refCode="refCode" @refreshPlanDetail="refreshPlanDetail"></AddPlan>
        <ModalGenerateBill v-if="isGenerateBill"></ModalGenerateBill>
        <ClientListingPopup v-if="isOpenClientListPopup"  :CodePlan="CodePlan"  :planId="PlanId" reqFrom="PLAN" serviceId="0"
            @close="isOpenClientListPopup = false; PlanId = 0;"></ClientListingPopup>
        <PlanHistory v-if="isPlanPopup" :planId="planId" @close="isPlanPopup = false"></PlanHistory>
        <ViewPlanHistory v-if="isPlanviewDetailPopup" :planId="planId"  :plan-code1="planCode1" @close="isPlanviewDetailPopup = false"></ViewPlanHistory>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import Schema from '../Plan/PlanSchema'
import AddPlan from '../../components/Modals/AddPlan.vue';
import ModalGenerateBill from '../../components/Modals/ModalGenerateBill.vue';
import ClientListingPopup from '../../components/Modals/ClientListingPopup.vue';
import PlanHistory from '../../components/Modals/PlanHistory.vue';
import ViewPlanHistory from '../../components/Modals/ViewPlanHistory.vue'
import Vue from 'vue'
export default ({
    components: { AddPlan, ModalGenerateBill, ClientListingPopup, PlanHistory,ViewPlanHistory },
    data() {
        return {

            Headers: [
                {
                    COLUMN_NAME: "plan_image_url",
                    DATA_TYPE: "image",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Image",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "plan_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('PlanName'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "invoice_template",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('InvoiceTemplate'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "customer_segment",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: false,
                    DISPLAY_NAME: this.$t('CustomerType'),
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "effective_date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('EffectiveDate'),
                    DISPLAY_ORDER: 5,

                },
                {
                    COLUMN_NAME: "expiration_date",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('ExpirationDate'),
                    DISPLAY_ORDER: 6,

                },
                {
                    COLUMN_NAME: "client_count",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('clientmapped'),
                    DISPLAY_ORDER: 6
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedDate'),
                    DISPLAY_ORDER: 7,
                },
                {
                    COLUMN_NAME: "created_by",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedBy'),
                    DISPLAY_ORDER: 8,
                },
                {
                    COLUMN_NAME: "payment_type",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('BillingFrequency'),
                    DISPLAY_ORDER: 9,
                },
                
                {
                    COLUMN_NAME: "status_id",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('Status'),
                    DISPLAY_ORDER: 10,

                }

            ],
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            list: [],
            changesDateFormat: "",
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            searchCondition: '',
            GroupStart: 1,
            GroupEnd: 3,
            PlanId: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortExp: '',
            PlanListing: [],
            isEditPlan: false,
            isGenerateBill: false,
            noRecords: false,
            BillingTypeOptions: [],
            isOpenClientListPopup: false,
            EditPlanDetail: [],
            refCode: '',
            refreshComponent: 0,
            isPlanPopup: false,
            isPlanviewDetailPopup:false,
            planCode1:'',
            CodePlan:''

        }
    },
    created: async function () {

        var vm = this;
        await vm.GetExtraFeatures();
        Schema.bindleftCommonSearchdropdown(this);
        await vm.FetchData();
        vm.EnableCustomerTypeColumn();


    },
    methods: {
        getRowClass(data) {
            const today = new Date().setHours(0, 0, 0, 0);
            const expDate = new Date(data.row.expiration_date).setHours(0, 0, 0, 0);
            //if (expDate <= today) {
            return 'high-profile-bar'; // This is your red highlight class
            //}
            //return '';
        },
        OpenClientListPopup: function (id,code) {
            debugger
            this.PlanId = id;
            this.CodePlan= code;
            this.isOpenClientListPopup = !this.isOpenClientListPopup;
        },
        EnableCustomerTypeColumn() {
            var vm = this;
            if (vm.BillingTypeOptions[0].FEATURE_CODE != 'SUBSCRIPTION_BILLING') {
                vm.Headers[3].VISIBLE = true;
            }
        },
        GetExtraFeatures: async function () {
            var vueObj = this;
            var url = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            await DataService.CheckExtraFeatures(url).then(function (response) {
                var parseData = JSON.parse(response.data);
                vueObj.BillingTypeOptions = parseData;
            });
        },
        FetchData: async function () {
            var vueObj = this;
            vueObj.PlanListing = [];
            var url = `pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&search=${encodeURIComponent(vueObj.searchCondition)}`;
            await DataService.GetPlanListing(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'expiration_date') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'client_count') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'effective_date') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'status_id') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'plan_name') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'created_at') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'plan_image_url') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                });
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.PlanListing = response.data.result.data;
                        vueObj.PlanListing.forEach((element) => {
                            ['effective_date', 'expiration_date', 'created_at'].forEach((key) => {
                                if (element[key]) {
                                    element[key] = new Date(element[key]);
                                }
                            });
                        });
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.PlanListing.forEach(plan => {
                            Vue.set(plan, "isSelected", false);
                            if (plan.client_count > 0) {
                                Vue.set(plan, "isCheckBoxDisabled", true);
                            }
                            else {
                                Vue.set(plan, "isCheckBoxDisabled", false);
                            }
                        });

                        vueObj.PlanListing.forEach((row) => {
                            const today = new Date().setHours(0, 0, 0, 0);
                            const expDate = new Date(row.expiration_date).setHours(0, 0, 0, 0);

                            if (expDate < today && row.expiration_date !=null) {
                                // Append class safely without leading spaces
                                row.additionalClass = (row.additionalClass || '').trim();
                                row.additionalClass += (row.additionalClass ? ' ' : '') + 'high-profile-bar';
                            }
                        });
                        vueObj.noRecord = false;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.PlanListing = [];
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.PlanListing = [];
                }
            })
        },
        leftCommonSearch(value) {
            console.log("value", value)
            this.pageNum = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod(value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        OpenPlanDetail: function (id, code) {
            debugger
            var vm = this;
            vm.PlanId = id;
            if (code == "SUBSCRIPTION_BILLING") {
                vm.$router.push({ name: 'ManageSubscriptionPlanDetail', params: { id: vm.PlanId } });
                // window.open(vm.$router.resolve({ name: 'ManageSubscriptionPlanDetail', params: { id: vm.PlanId } }).href, '_blank');
            } else {
                vm.$router.push({ name: 'ManagePlan', params: { id: vm.PlanId } });
                // window.open(vm.$router.resolve({ name: 'ManagePlan', params: { id: vm.PlanId } }).href, '_blank');
            }
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "GenerateBill":
                    this.GenerateBill();
                    break;
                case "Add":
                    this.AddNew();
                    break;
                case "Delete":
                    this.DeletePlan();
                    break;
            }
        },
        AddNew() {
            debugger;
        this.EditPlanDetail = {};     
        this.refCode = '';            
        this.isEditPlan = true;      
    },
        GenerateBill: function () {
            var vm = this;
            window.open(vm.$router.resolve({ name: 'BatchProcessing' }).href, '_blank');
            // vm.isGenerateBill = true;
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        DeletePlan: function (planId) {
            debugger;

            var vm = this;
            var val = "";
            if (planId != null || planId != undefined) {
                val = planId.toString();
            }
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0 ) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Plan")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "BILLING",
                        refCode: "TALYGEN_PLANS"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Plan')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Plan')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Plan')]), "failure", true, vm.$t('Alert'));
            }
        },
        sortdata: function (sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Clone':
                        action.isVisible = true;
                        break;
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'History':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'ClonePlan':
                    this.ClonePlan(item);
                    break;
                case 'EditPlan':
                    this.EditPlan(item);
                    break;
                case 'DeletePlan':
                    this.DeletePlan(item.id);
                    break;
                case 'PlanHistory':
                    this.PlanHistory(item)
                    break;
                case 'PlanviewDetailHistory':
                    this.PlanviewDetailHistory(item)
                    break;  
            }
        },
        ClonePlan(item) {
            
            var vm = this;
            vm.BindPlanDetails(item.id, 'Clone');
        },
        EditPlan(item) {
            debugger
            var vm = this;
            vm.refCode = 'Update';
            vm.BindPlanDetails(item.id, 'Edit');
        },
        PlanviewDetailHistory(data){
          debugger;
          var vm =this;
          vm.planId = data.id;
          vm.planCode1 =data.plan_code;
          vm.isPlanviewDetailPopup = true;
        },
        BindPlanDetails: function (planId, isFor) {
          debugger
            var vm = this;
            var url = `planId=${planId}`;
            DataService.displayPlanDetails(url).then(response => {

                if (response.data.status == 'Success') {
                    if (JSON.parse(response.data.result).data.length > 0) {
                        vm.EditPlanDetail = JSON.parse(response.data.result).data;
                        // ;
                        if (isFor == 'Edit') {
                            vm.isEditPlan = true;
                            vm.refCode = 'Update';
                        }
                        else if (isFor == 'Clone') {
                            vm.isEditPlan = true;
                            vm.refCode = 'Clone';
                        }
                        // vm.isCheckEditPlan = true;
                        // vm.refCode = 'Update';
                    }
                }
            })
        },
        PlanHistory(data) {

            var vm = this;
            vm.planId = data.id;
            vm.isPlanPopup = true;
        }

    }
})
</script>