<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex">
                            <h5 class="modal-title">{{ $t('GenerateBills') }}</h5>
                            <button type="button" class="close" v-on:click="Close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <dynamic-form lang="en" ref="validateGenerateBill" :buttons="false" :schema="FormSchema">
                                <template v-slot:tgslot-ReadingDateMonth="{ data }">
                                    <div class="form-group mb-0">
                                        <vc-date-picker is-range class="timedate input-group" v-model="monthRange"
                                            @input="onMonthSelected">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input placeholder="M/D/YYYY"
                                                    :class="{ 'form-control': true, 'is-invalid': ismonthlyDateValid }"
                                                    :value="inputValue.start + ' - ' + inputValue.end"
                                                    v-on="inputEvents" />
                                                <div class="input-group-append" v-on:click="togglePopover">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em>
                                                    </span>
                                                </div>
                                            </template>
                                        </vc-date-picker>
                                    </div>
                                </template>
                            </dynamic-form>
                            <div class="row flex-row-reverse mt-3">
                                <div class="col-lg-12 text-right">
                                    <a href="javascript:;" v-show="btnViewLog" class="mr-2 float-left" title="View Log"
                                        v-on:click="fnViewLogBill()"><i class="fa fa-eye pr-2"></i>{{ batchName }}</a>
                                    <!-- <a href="javascript:;" v-show="btnViewLog" class="btn btn-info mr-2 float-left" v-on:click="fnViewLogBill()"
                                          title="View Log"><i class="fa fa-eye pr-2"></i>{{ $t('ViewLog') }}</a> -->
                                    <a href="javascript:;" class="btn btn-success save-btn mr-2"
                                        v-on:click="fnGenerateBill()" title="Save"><i class="fa fa-save pr-2"></i>{{
                                            $t('GenerateBills') }}</a>
                                    <a href="javascript:;" class="btn btn-danger" v-on:click="Close()" title="Cancel"><i
                                            class="fa fa-close pr-2"></i>{{ $t('Close') }}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
    data() {
        return {
            isLoading: false,
            ismonthlyDateValidc: false,
            btnViewLog: false,
            monthStartDate: new Date(),
            requestId: '',
            batchName: '',
            monthEndDate: new Date(),
            monthRange: {
                start: this.monthStartDate,
                end: this.monthEndDate,
            },
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Plan'),
                            name: "Plan",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectPlan'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SlotField",
                            label: "Reading Date Month",
                            name: "ReadingDateMonth",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        }

                    ]
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        setTimeout(function () {
            vm.GetDDLPlanData();
        }, 300);
        vm.monthRange = {
            start: vm.monthStartDate,
            end: vm.monthEndDate,
        };

    },
    methods: {
        Close: function () {
            var vueObj = this;
            vueObj.$parent.isGenerateBill = false;
        },
        togglePopover() { },
        async onMonthSelected(date) {
            var vm = this;
            vm.updateMonthRange = false;
            vm.monthStartDate = date.start;
            vm.monthEndDate = date.end;
            var todayDate = new Date();
            var currentMonthDays = new Date(
                todayDate.getFullYear(),
                todayDate.getMonth() + 1,
                0
            ).getDate();
            const thirtyDaysLater = new Date(vm.monthStartDate);
            thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);
            const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
            const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
            var dateBeforeThirtyDays = new Date();
            dateBeforeThirtyDays.setDate(
                dateBeforeThirtyDays.getDate() - currentMonthDays
            );
            if (diffDays == 27 || diffDays == 28 || diffDays == 29 || diffDays == 30) {
                vm.ismonthlyDateValid = false;
            }
            else if (diffDays < 27) {
                vm.ismonthlyDateValid = true;
            }
            else {
                vm.ismonthlyDateValid = true;
                vm.ShowAlert(
                    "You can not select more than 31 days.",
                    "warning",
                    false,
                    vm.$t("Alert")
                );
            }
        },
        GetDDLPlanData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=PLANS&search=&refId=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validateGenerateBill.updateProperty("Plan", "config.options", response.data.DATA);
                        vm.$refs.validateGenerateBill.UpdateKeyValue();
                    }
                }
            })
        },
        fnViewLogBill() {
            var vm = this;
            window.open(vm.$router.resolve({ name: 'InvoiceLog', params: { requestId: vm.requestId } }).href, '_blank');
        },
        fnGenerateBill: async function () {
            var vm = this;
            const result = await vm.$refs.validateGenerateBill.validateForm.validate();
            if (result) {
                var planId = vm.$refs.validateGenerateBill.getPropertyValue("Plan", "value");
                var fromDate = vm.monthRange.start;
                var toDate = vm.monthRange.end;
                var formattedFromDate = fromDate.toISOString().split('T')[0]; // 'YYYY-MM-DD'
                var formattedToDate = toDate.toISOString().split('T')[0]; // 'YYYY-MM-DD'
                var url = `planId=${planId}&fromDate=${formattedFromDate}&toDate=${formattedToDate}`;
                await DataService.GetGenerateBillListing(url).then(function (response) {
                    debugger;
                    if (response.data.result.status_message == 'success') {
                        vm.ShowAlert('Invoice Generated Successfully', "Success", true, vm.$t("Alert"));
                        vm.btnViewLog = true;
                        vm.requestId = response.data.result.request_id;
                        vm.batchName = response.data.result.batch_name;
                    }
                    else {
                        vm.ShowAlert('Invoice Generation Failed', "damger", true, vm.$t("Alert"));
                        vm.btnViewLog = false;
                    }

                });
            }
        }


    }
}
</script>
