<template>
  <div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div
            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center"
          >
            <div>
              <h4 class="modal-title mb-0">Plan History Details</h4>
            </div>
            <button type="button" class="close ml-3" @click="Close">
              <em class="fa fa-times" aria-hidden="true"></em>
            </button>
          </div>

          <!-- Tabs -->
          <div class="col-12 mt-4 ">
            <div class="main-content ">
              <div class="col-12 mb-4 p-0">
                <ul
                  class="nav nav-pills flex-row left-tab responivecustomtab_bod"
                >
                  <li v-for="tab in tabs" :key="tab.name" class="nav-item">
                    <a
                      href="#"
                      class="nav-link left-menu px-4"
                      :class="{ active: activeTab === tab.name }"
                      @click.prevent="activeTab = tab.name"
                    >
                      {{ tab.label }}
                    </a>
                  </li>
                </ul>

                <div class="tab-content mb-4 border p-3">
                  <div
                    class="col-12 mt-0 p-0"
                    v-if="!['Services', 'Subsidy', 'Rates'].includes(activeTab)"
                  >
                    <div class="form-group">
                      <label>Version:</label>
                      <select
                        v-model="selectedVersion"
                        @change="onVersionChange"
                        class="form-control"
                      >
                        <option
                          v-for="version in versionOptions"
                          :key="version"
                          :value="version"
                        >
                          Version {{ version }}
                        </option>
                      </select>
                    </div>
                  </div>

                  <div class="history-container" v-if="activeTab === 'Plan'">
                    <!-- Current Version -->
                    <div class="history-table">
                      <div class="col-lg-12 mb-3 p-0">
                        <div class="heading-border mt-2">
                          <h5 class="h5">
                            <span
                              >Current Version:
                              <span v-if="selectedVersion" class="border-0">{{
                                selectedVersion
                              }}</span></span
                            >
                          </h5>
                        </div>
                      </div>
                      <div v-if="isLoading" class="loading">Loading...</div>
                      <div
                        v-else-if="PlanHistoryList.length === 0"
                        class="no-records"
                      >
                        No record(s) found
                      </div>

                      <div class="table-responsive" v-else>
                        <table class="table table-bordered mt-0 mb-0">
                          <tbody>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Plan Name</td>
                              <td>{{ item.CURRENT_PLAN_NAME || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Billing Type</td>
                              <td>{{ item.CURRENT_PLAN_CODE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Meter Type</td>
                              <td>{{ item.CURRENT_METER_TYPE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Invoice Template
                              </td>
                              <td>
                                {{ item.CURRENT_INVOICE_TEMPLATE || "N/A" }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Customer Type</td>
                              <td>{{ item.CURRENT_CUSTOMER_SEGMENT }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Status</td>
                              <td>{{ item.CURRENT_STATUS_NAME }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Show on Website
                              </td>
                              <td>{{ item.CURRENT_IS_SHOW_ON_WEBSITE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Show Pricing</td>
                              <td>{{ item.CURRENT_IS_SHOW_ON_PRICING }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Plan Type</td>
                              <td>{{ item.CURRENT_IS_FOR_PLAN }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Effective Date</td>
                              <td>
                                {{
                                  formatDate(item.CURRENT_EFFECTIVE_DATE) ||
                                  "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Expiration Date
                              </td>
                              <td>
                                {{
                                  formatDate(item.CURRENT_EXPIRATION_DATE) ||
                                  "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Create Date</td>
                              <td>
                                {{
                                  formatDate(item.CURRENT_VERSION_DATE) || "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Create By</td>
                              <td>{{ item.CREATED_BY || "N/A" }}</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>

                    <!-- Previous Version -->
                    <div class="history-table">
                      <div class="col-lg-12 mb-3 p-0">
                        <div class="heading-border mt-2">
                          <h5 class="h5">
                            <span
                              >Previous Version:
                              <span
                                class="border-0"
                                v-if="
                                  PlanHistoryList.length &&
                                  PlanHistoryList[0].PREVIOUS_VERSION
                                "
                                >{{ PlanHistoryList[0].PREVIOUS_VERSION }}</span
                              ></span
                            >
                          </h5>
                        </div>
                      </div>
                      <div v-if="isLoading" class="loading">Loading...</div>

                      <div class="table-responsive" v-else>
                        <table class="table table-bordered mt-0 mb-0">
                          <tbody
                            v-if="
                              PlanHistoryList.length &&
                              PlanHistoryList[0].PREVIOUS_PLAN_NAME
                            "
                          >
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Plan Name</td>
                              <td>{{ item.PREVIOUS_PLAN_NAME || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Billing Type</td>
                              <td>{{ item.PREVIOUS_PLAN_CODE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Meter Type</td>
                              <td>{{ item.PREVIOUS_METER_TYPE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Invoice Template
                              </td>
                              <td>
                                {{ item.PREVIOUS_INVOICE_TEMPLATE || "N/A" }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Customer Type</td>
                              <td>{{ item.PREVIOUS_CUSTOMER_SEGMENT }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Status</td>
                              <td>{{ item.PREVIOUS_STATUS_NAME }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Show on Website
                              </td>
                              <td>{{ item.PREVIOUS_IS_SHOW_ON_WEBSITE }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Show Pricing</td>
                              <td>{{ item.PREVIOUS_IS_SHOW_ON_PRICING }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Plan Type</td>
                              <td>{{ item.PREVIOUS_IS_FOR_PLAN }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Effective Date</td>
                              <td>
                                {{
                                  formatDate(item.PREVIOUS_EFFECTIVE_DATE) ||
                                  "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">
                                Expiration Date
                              </td>
                              <td>
                                {{
                                  formatDate(item.PREVIOUS_EXPIRATION_DATE) ||
                                  "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Create Date</td>
                              <td>
                                {{
                                  formatDate(item.PREVIOUS_VERSION_DATE) ||
                                  "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PlanHistoryList"
                              :key="index"
                            >
                              <td class="font-weight-medium">Create By</td>
                              <td>{{ item.CREATED_BY || "N/A" }}</td>
                            </tr>
                          </tbody>

                          <tbody v-else>
                            <tr>
                              <td
                                colspan="6"
                                class="text-danger text-center fw-bold"
                              >
                                No record(s) found
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>

                  <div v-if="activeTab === 'Services'">
                    <div v-if="isLoading">Loading...</div>
                    <div v-else-if="ServicePlanListing.length === 0">
                      No service plans found.
                    </div>
                    <div class="py-2 d-inline-block">
                      <span class="legend-ovrdue colorboxsquare"></span>
                      {{ $t("Deleted") }}
                    </div>

                    <div class="listing">
                      <div class="table-responsive table-fix-header">
                        <table class="table table-bordered">
                          <thead class="thead-bg">
                            <tr>
                              <th>Service Name</th>
                              <th>Service Code</th>
                              <!-- Conditionally render "Usage Measurement" and "Billing Rule Name" columns based on planCode1 -->
                              <th v-if="planCode1 !== 'SUBSCRIPTION_BILLING'">
                                Usage Measurement
                              </th>
                              <th v-if="planCode1 !== 'SUBSCRIPTION_BILLING'">
                                Billing Rule Name
                              </th>
                              <th>Created By</th>
                              <th>Created At</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr
                              v-for="(service, index) in ServicePlanListing"
                              :key="index"
                              :class="[
                                service.statusClass,
                                'odd',
                                service.status_id === 1003 ? 'danger-bar' : '',
                              ]"
                            >
                              <td>{{ service.service_name }}</td>
                              <td>{{ service.service_code }}</td>
                              <!-- Conditionally render the "Usage Measurement" and "Billing Rule Name" columns based on planCode1 -->
                              <td v-if="planCode1 !== 'SUBSCRIPTION_BILLING'">
                                {{ service.usage_measurement }}
                              </td>
                              <td v-if="planCode1 !== 'SUBSCRIPTION_BILLING'">
                                <div
                                  v-if="
                                    service.RuleDetails &&
                                    service.RuleDetails.length
                                  "
                                >
                                  <div
                                    v-for="(rule, idx) in service.RuleDetails"
                                    :key="idx"
                                  >
                                    {{ rule.billing_config_name }}
                                  </div>
                                </div>
                                <div v-else>--</div>
                              </td>
                              <td>{{ service.created_by }}</td>
                              <td>{{ formatDate(service.created_at) }}</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>

                  <!-- Rates Tab -->
                  <div v-if="activeTab === 'Rates'" class="listing">
                    <!--  Case 1: Non-Subscription Billing -->
                    <div v-if="planCode1 == 'UTILITY_BILLING'">
                      <div class="py-2 d-inline-block"></div>
                      <span class="legend-ovrdue colorboxsquare"></span>
                      {{ $t("Deleted") }}

                      <div class="table-responsive table-fix-header">
                        <table class="table table-bordered">
                          <thead class="thead-bg">
                            <tr>
                              <th>Rate Code</th>
                              <th>Service Name</th>
                              <th class="text-center">Rate</th>
                              <th>Start Date</th>
                              <th>End Date</th>
                              <th>Created By</th>
                              <th>Created At</th>
                              <th>Modified At</th>
                            </tr>
                          </thead>

                          <tbody
                            v-if="
                              RateCodeHistoryList &&
                              RateCodeHistoryList.length > 0
                            "
                          >
                            <tr
                              v-for="(item, index) in RateCodeHistoryList"
                              :key="index"
                              :class="[
                                item.statusClass,
                                'odd',
                                item.status_id === 1003 ? 'danger-bar' : '',
                              ]"
                            >
                              <td>
                                {{
                                  item.WC && item.WC.length
                                    ? item.WC[0].workcode_name
                                    : "N/A"
                                }}
                              </td>
                              <td>{{ item.service_name || "N/A" }}</td>
                              <td class="text-center">
                                {{ item.rate || "N/A" }}
                              </td>
                              <td>
                                {{ formatDate(item.start_date) || "N/A" }}
                              </td>
                              <td>{{ formatDate(item.end_date) || "N/A" }}</td>
                              <td>
                                {{
                                  item.WC &&
                                  item.WC.length > 0 &&
                                  item.WC[0].TTS &&
                                  item.WC[0].TTS.length > 0
                                    ? item.WC[0].TTS[0].created_by
                                    : "N/A"
                                }}
                              </td>
                              <td>
                                {{ formatDate(item.created_at) || "N/A" }}
                              </td>
                              <td>
                                {{ formatDate(item.modified_at) || "N/A" }}
                              </td>
                            </tr>
                          </tbody>

                          <tbody v-else>
                            <tr>
                              <td
                                colspan="8"
                                class="text-danger text-center fw-bold"
                              >
                                No record(s) found.
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>

                    <!--  Case 2: Subscription Billing -->
                    <div v-if="planCode1 == 'SUBSCRIPTION_BILLING'">
                      <div class="py-2 d-inline-block"></div>
                      <span class="legend-ovrdue colorboxsquare"></span>
                      {{ $t("Deleted") }}

                      <div class="table-responsive table-fix-header">
                        <table class="table table-bordered">
                          <thead class="thead-bg">
                            <tr>
                              <th>Service Name</th>
                              <th>Service Type</th>
                              <th>Billing Type</th>
                              <th>Billing Interval</th>
                              <th>From</th>
                              <th>To</th>
                              <th class="text-center">Rate</th>
                              <th>Start Date</th>
                              <th>End Date</th>
                              <th>Created At</th>
                            </tr>
                          </thead>

                          <tbody
                            v-if="
                              PlanRateSubscriptionDeatil &&
                              PlanRateSubscriptionDeatil.length > 0
                            "
                          >
                            <tr
                              v-for="(
                                item, index
                              ) in PlanRateSubscriptionDeatil"
                              :key="index"
                              :class="[
                                item.status_id === 1003 ? 'danger-bar' : '',
                                'odd',
                              ]"
                            >
                              <td>{{ item.service_name || "N/A" }}</td>
                              <td>{{ item.service_type_name || "N/A" }}</td>
                              <td>{{ item.billing_type || "N/A" }}</td>
                              <td>{{ item.billing_interval || "N/A" }}</td>
                              <td>{{ item.from || "N/A" }}</td>
                              <td>{{ item.to || "N/A" }}</td>
                              <td class="text-center">
                                {{ item.rate || "N/A" }}
                              </td>
                              <td>
                                {{
                                  formatDate(item.effective_start_date) || "N/A"
                                }}
                              </td>
                              <td>
                                {{
                                  formatDate(item.effective_end_date) || "N/A"
                                }}
                              </td>
                              <td>
                                {{ formatDate(item.created_at) || "N/A" }}
                              </td>
                            </tr>
                          </tbody>

                          <tbody v-else>
                            <tr>
                              <td
                                colspan="8"
                                class="text-danger text-center fw-bold"
                              >
                                No record(s) found.
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>

                  <div
                    class="history-container table-responsive table-fix-header"
                    v-if="activeTab === 'BillingCycle'"
                  >
                    <!--  Current Version for SUBSCRIPTION_BILLING -->
                    <div
                      v-if="planCode1 === 'SUBSCRIPTION_BILLING'"
                      class="history-table"
                    >
                      <div class="col-lg-12 mb-3 p-0">
                        <div class="heading-border mt-2">
                          <h5 class="h5"><span>Current Version</span></h5>
                        </div>
                      </div>

                      <div v-if="isLoading" class="loading">Loading...</div>
                      <div
                        v-else-if="CurrentBillCycleList.length === 0"
                        class="no-records"
                      >
                        No record(s) found
                      </div>
                      <div v-else class="table-responsive table-fix-header">
                        <table class="table table-bordered mb-0">
                          <tbody>
                            <!-- Only showing specific columns for SUBSCRIPTION_BILLING -->
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-planname-' + index"
                            >
                              <td class="font-weight-medium">Plan Name</td>
                              <td>{{ item.plan_name || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-daysuntildue-' + index"
                            >
                              <td class="font-weight-medium">Days Until Due</td>
                              <td>{{ item.days_until_due || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-latefeeamount-' + index"
                            >
                              <td class="font-weight-medium">
                                Late Fee Amount
                              </td>
                              <td>{{ item.late_fee_amount || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-latefeesapplicable-' + index"
                            >
                              <td class="font-weight-medium">
                                Late Fees Applicable
                              </td>
                              <td>
                                {{ item.late_fees_applicable ? "Yes" : "No" }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-latefeetype-' + index"
                            >
                              <td class="font-weight-medium">Late Fee Type</td>
                              <td>
                                {{
                                  item.late_fee_type === 1
                                    ? "Flat Fee"
                                    : item.late_fee_type === 2
                                    ? "Percentage Fee"
                                    : "N/A"
                                }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-endtime-' + index"
                            >
                              <td class="font-weight-medium">Created At</td>
                              <td>
                                {{ formatDate(item.created_at || "N/A") }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in CurrentBillCycleList"
                              :key="'current-endtime-' + index"
                            >
                              <td class="font-weight-medium">Created By</td>
                              <td>{{ item.created_by || "N/A" }}</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>

                    <!--  Previous Version for SUBSCRIPTION_BILLING -->
                    <div
                      v-if="planCode1 === 'SUBSCRIPTION_BILLING'"
                      class="history-table"
                    >
                      <div class="col-lg-12 mb-3 p-0">
                        <div class="heading-border mt-2">
                          <h5 class="h5"><span>Previous Version</span></h5>
                        </div>
                      </div>

                      <div v-if="isLoading" class="loading">Loading...</div>
                      <div
                        v-else-if="PreviousBillCycleList.length === 0"
                        class="no-records"
                      >
                        No record(s) found
                      </div>
                      <div v-else class="table-responsive table-fix-header">
                        <table class="table table-bordered mb-0">
                          <tbody>
                            <!-- Only showing specific columns for SUBSCRIPTION_BILLING -->
                            <tr
                              v-for="(item, index) in PreviousBillCycleList"
                              :key="'previous-planname-' + index"
                            >
                              <td class="font-weight-medium">Plan Name</td>
                              <td>{{ item.plan_name || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PreviousBillCycleList"
                              :key="'previous-daysuntildue-' + index"
                            >
                              <td class="font-weight-medium">Days Until Due</td>
                              <td>{{ item.days_until_due || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PreviousBillCycleList"
                              :key="'previous-latefeeamount-' + index"
                            >
                              <td class="font-weight-medium">
                                Late Fee Amount
                              </td>
                              <td>{{ item.late_fee_amount || "N/A" }}</td>
                            </tr>
                            <tr
                              v-for="(item, index) in PreviousBillCycleList"
                              :key="'previous-latefeesapplicable-' + index"
                            >
                              <td class="font-weight-medium">
                                Late Fees Applicable
                              </td>
                              <td>
                                {{ item.late_fees_applicable ? "Yes" : "No" }}
                              </td>
                            </tr>
                            <tr
                              v-for="(item, index) in PreviousBillCycleList"
                              :key="'previous-latefeetype-' + index"
                            >
                              <td class="font-weight-medium">Late Fee Type</td>
                              <td>
                                {{
                                  item.late_fee_type === 1
                                    ? "Flat Fee"
                                    : item.late_fee_type === 2
                                    ? "Percentage Fee"
                                    : "N/A"
                                }}
