<script>
import DataService from "@/services/DataService";

export default {
  props: {
    planId: {
      type: Number,
      required: true,
    },
    planCode1: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      activeTab: "Plan",
      isLoading: false,
      PlanHistoryList: [],
      ServicePlanListing: [],
      TotalRecords: 0,
      PageSize: 10,
      PageNumber: 1,
      showAll: true,
      SortBy: "",
      SortExp: "",
      searchCondition: "",
      versionNo: "",
      RateCodeHistoryList: [],
      CurrentRateCodeList: [],
      PreviousRateCodeList: [],
      CurrentBillCycleList: [],
      PreviousBillCycleList: [],
      fullPlanHistoryList: [],
      PlanRateSubscriptionDeatil: [],
      BillData: [],
      SubsidyData: [],
      ishistory: true,
      versionOptions: [],
      selectedVersion: null,
      // planCode1:'',
      planCode: "UTILITY_BILLING",
      tabs: [
        { name: "Plan", label: this.$t("Plan") },
        { name: "Services", label: this.$t("Services") },
        { name: "Rates", label: this.$t("Rates") },
        { name: "BillingCycle", label: this.$t("Billing Cycle") },
        // { name: "PaymentInstallmentSettings", label: this.$t("Payment Installment Settings") },
      ].concat(
        // Only add Subsidy tab if planCode1 is not 'SUBSCRIPTION_BILLING'
        this.planCode1 !== "SUBSCRIPTION_BILLING"
          ? [{ name: "Subsidy", label: this.$t("Subsidy") }]
          : []
      ),
    };
  },

  created() {
    debugger;
    //  this.updateTabs()
  },
  mounted() {
    if (this.activeTab === "Plan") {
      this.getPlanHistory(1);
    }
  },
  watch: {
    activeTab(newTab) {
      debugger;

      this.PlanHistoryList = [];
      this.ServiceHistoryList = [];
      this.RateHistoryList = [];
      this.BillingCycleList = [];
      this.SubsidyList = [];

      if (newTab === "Plan") {
        this.getPlanHistory(1);
      } else if (newTab === "Services") {
        if (this.planCode1 === "SUBSCRIPTION_BILLING") {
          this.FetchSubscriptionBillingServiceData();
        } else {
          this.FetchRecords();
        }
      } else if (newTab === "Rates") {
        if (this.planCode1 === "SUBSCRIPTION_BILLING") {
          this.GetPlanSubscriptionRateListing();
        } else {
          this.FetchRateCodeHistory(1);
        }
      } else if (newTab === "BillingCycle") {
        this.GetBillCycleData(1, this.selectedVersion, 1);
      } else if (newTab === "Subsidy") {
        this.GetSubsidyPlanData();
      }
    },
  },

  methods: {
    Close() {
      this.$parent.isPlanviewDetailPopup = false;
    },
    formatDate(date) {
      if (!date) return "N/A";

      const d = new Date(date);

      if (isNaN(d) || d.getFullYear() === 1900) {
        return "N/A";
      }

      return d.toLocaleString();
    },

    onVersionChange() {
      debugger;
      if (this.selectedVersion) {
        //  Find the selected version data
        const selectedData = this.fullPlanHistoryList.find(
          (item) => item.CURRENT_VERSION === this.selectedVersion
        );

        //  Update plan history info
        this.PlanHistoryList = selectedData ? [selectedData] : [];
        this.previousVersion = selectedData?.PREVIOUS_VERSION || null;

        //  Format date fields
        this.PlanHistoryList.forEach((element) => {
          [
            "CURRENT_EFFECTIVE_DATE",
            "CURRENT_EXPIRATION_DATE",
            "CURRENT_VERSION_DATE",
            "PREVIOUS_EFFECTIVE_DATE",
            "PREVIOUS_EXPIRATION_DATE",
            "PREVIOUS_VERSION_DATE",
          ].forEach((key) => {
            if (element[key]) element[key] = new Date(element[key]);
          });
        });

        //  Refresh active tab data only
        if (this.activeTab === "Rates") {
          this.FetchRateCodeHistory(1);
        } else if (this.activeTab === "BillingCycle") {
          this.GetBillCycleData(1, this.selectedVersion);
        }
      } else {
        this.PlanHistoryList = [];
        this.previousVersion = null;

        if (this.activeTab === "Rates") {
          this.FetchRateCodeHistory();
        } else if (this.activeTab === "BillingCycle") {
          this.GetBillCycleData();
        }
      }
    },
    // updateTabs(item) {
    //   debugger
    //       if (item.plan_code === "SUBSCRIPTION_BILLING") {
    //         this.tabs = this.tabs.filter(tab => tab.name !== "Subsidy");
    //       }
    //     },
    GetSubsidyPlanData: async function (includeStatus = 1) {
      debugger;
      var vm = this;

      let param = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.searchCondition}&PlanId=${vm.planId}&includeStatus=${includeStatus}`;

      DataService.GetSubsidyPlanData(param)
        .then(async (response) => {
          console.log("Response Data:", response.data);

          if (response && response.data && response.data[0]) {
            try {
              const parsedResult = JSON.parse(response.data[0]);
              vm.SubsidyData =
                parsedResult.Data && parsedResult.Data.length
                  ? parsedResult.Data
                  : [];
            } catch (err) {
              console.error("JSON Parse Error:", err);
            }
          }
        })
        .catch((error) => {
          console.error("Error fetching subsidy plan data:", error);
        });
    },
    async GetBillCycleData(ishistory = 1, versionNo = null, count) {
      debugger;
      this.isLoading = true;
      this.CurrentBillCycleList = [];
      this.PreviousBillCycleList = [];

      const param = `planid=${this.planId}&ishistory=${ishistory}&versionNo=${
        versionNo || this.selectedVersion || ""
      }`;

      try {
        const response = await DataService.GetBillingCycleData(param);

        if (response?.data?.result) {
          const parsed = JSON.parse(response.data.result);

          const currentData = parsed.CURRENT || [];
          const previousData = parsed.PREVIOUS || [];
          const totalVersion = parsed.TOTAL_VERSION || 0;

          this.versionOptions = Array.from(
            { length: totalVersion },
            (_, i) => totalVersion - i
          );

          if (count === 1 && this.versionOptions.length > 0) {
            this.selectedVersion = this.versionOptions[0];
            debugger;
            this.GetBillCycleData(ishistory, this.selectedVersion, 0);
            return;
          }

          const parseDates = (items, keys) => {
            items.forEach((item) => {
              keys.forEach((key) => {
                if (item[key]) {
                  const d = new Date(item[key]);
                  item[key] = isNaN(d.getTime()) ? item[key] : d;
                }
              });
            });
          };

          parseDates(currentData, [
            "cycle_start_date",
            "cycle_end_date",
            "created_at",
            "modified_at",
          ]);
          parseDates(previousData, [
            "cycle_start_date",
            "cycle_end_date",
            "created_at",
            "modified_at",
          ]);

          this.CurrentBillCycleList = currentData;
          this.PreviousBillCycleList = previousData;
          this.BillData = [...currentData, ...previousData];
        } else {
          this.BillData = [];
          this.CurrentBillCycleList = [];
          this.PreviousBillCycleList = [];
          this.versionOptions = [];
        }
      } catch (error) {
        console.error("Error fetching billing cycle data:", error);
        this.BillData = [];
        this.CurrentBillCycleList = [];
        this.PreviousBillCycleList = [];
        this.BillingVersionOptions = [];
      } finally {
        this.isLoading = false;
      }
    },

    getPlanHistory(count) {
      debugger;
      this.isLoading = true;

      DataService.GetPlanHistoryListing(`planId=${this.planId}`)
        .then((response) => {
          if (response && response.data) {
            this.fullPlanHistoryList = response.data;

            const sortedData = [...response.data].sort(
              (a, b) =>
                new Date(b.CURRENT_VERSION_DATE) -
                new Date(a.CURRENT_VERSION_DATE)
            );

            this.versionOptions = sortedData.map((r) => r.CURRENT_VERSION);

            // Default: select first (latest) version
            debugger;
            if (this.versionOptions.length > 0 && count == 1) {
              this.selectedVersion = this.versionOptions[0];
              this.getPlanHistory(0);
            }

            const selectedData = sortedData.find(
              (item) => item.CURRENT_VERSION === this.selectedVersion
            );

            this.PlanHistoryList = selectedData ? [selectedData] : [];
            this.previousVersion = selectedData?.PREVIOUS_VERSION || null;

            this.PlanHistoryList.forEach((element) => {
              // Parsing the dates
              [
                "CURRENT_EFFECTIVE_DATE",
                "CURRENT_EXPIRATION_DATE",
                "CURRENT_VERSION_DATE",
                "PREVIOUS_EFFECTIVE_DATE",
                "PREVIOUS_EXPIRATION_DATE",
                "PREVIOUS_VERSION_DATE",
              ].forEach((key) => {
                if (element[key]) element[key] = new Date(element[key]);
              });

              // Convert boolean to Yes/No for website and pricing visibility
              element.CURRENT_IS_SHOW_ON_PRICING =
                element.CURRENT_IS_SHOW_ON_PRICING ? "Yes" : "No";
              element.CURRENT_IS_SHOW_ON_WEBSITE =
                element.CURRENT_IS_SHOW_ON_WEBSITE ? "Yes" : "No";
              element.PREVIOUS_IS_SHOW_ON_PRICING =
                element.PREVIOUS_IS_SHOW_ON_PRICING ? "Yes" : "No";
              element.PREVIOUS_IS_SHOW_ON_WEBSITE =
                element.PREVIOUS_IS_SHOW_ON_WEBSITE ? "Yes" : "No";
              element.CURRENT_IS_FOR_PLAN =
                element.CURRENT_IS_FOR_PLAN === 1
                  ? "Prepaid"
                  : element.CURRENT_IS_FOR_PLAN === 2
                  ? "Postpaid"
                  : "N/A";

              element.PREVIOUS_IS_FOR_PLAN =
                element.PREVIOUS_IS_FOR_PLAN === 1
                  ? "Prepaid"
                  : element.PREVIOUS_IS_FOR_PLAN === 2
                  ? "Postpaid"
                  : "N/A";
            });
          } else {
            this.PlanHistoryList = [];
            this.fullPlanHistoryList = [];
            this.versionOptions = [];
          }

          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
          this.ShowAlert(this.$t("UnknownError"), "failure", this.$t("Alert"));
        });
    },

    async FetchRecords() {
      debugger;
      this.isLoading = true;
      this.ServicePlanListing = [];

      const url = `planId=${this.planId}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&planCode=${this.planCode}&pagesize=${this.PageSize}&pagenumber=${this.PageNumber}&showAll=${this.showAll}`;

      try {
        const response = await DataService.GetPlanServiceListing(url);

        if (
          response &&
          response.data &&
          response.data.result &&
          response.data.result.data.length > 0
        ) {
          debugger;
          const dt = response.data.result.data;

          dt.forEach((item) => {
            item.isRuleView = false;
            item.rules = [];
            //item.statusClass = item.status_id === 1003 ? "text-danger" : "";
          });

          this.ServicePlanListing = dt;
          this.TotalRecords = dt[0].TOTALRECORDS || 0;
        } else {
          this.ServicePlanListing = [];
          this.TotalRecords = 0;
        }
      } catch (error) {
        this.ServicePlanListing = [];
        this.TotalRecords = 0;
        this.ShowAlert(this.$t("UnknownError"), "failure", this.$t("Alert"));
      } finally {
        this.isLoading = false;
      }
    },
    async FetchSubscriptionBillingServiceData() {
      debugger;
      this.isLoading = true;
      this.ServicePlanListing = [];

      const url = `planId=${this.planId}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&planCode=${this.planCode1}&pagesize=${this.PageSize}&pagenumber=${this.PageNumber}&showAll=${this.showAll}`;

      try {
        const response = await DataService.GetPlanServiceListing(url);

        if (
          response &&
          response.data &&
          response.data.result &&
          response.data.result.data.length > 0
        ) {
          debugger;
          const dt = response.data.result.data;

          dt.forEach((item) => {
            item.isRuleView = false;
            item.rules = [];
            //item.statusClass = item.status_id === 1003 ? "text-danger" : "";
          });

          this.ServicePlanListing = dt;
          this.TotalRecords = dt[0].TOTALRECORDS || 0;
        } else {
          this.ServicePlanListing = [];
          this.TotalRecords = 0;
        }
      } catch (error) {
        this.ServicePlanListing = [];
        this.TotalRecords = 0;
        this.ShowAlert(this.$t("UnknownError"), "failure", this.$t("Alert"));
      } finally {
        this.isLoading = false;
      }
    },

    async FetchRateCodeHistory() {
      debugger;
      this.isLoading = true;
      this.RateCodeHistoryList = [];

      const url = `PlanId=${this.planId}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}`;

      try {
        const response = await DataService.GetRateCodeHistoryList(url);
        debugger;
        if (response && response.data && response.data.result) {
          debugger;
          const historyData = response.data.result || [];

          const parseDates = (items, keys) => {
            items.forEach((item) => {
              keys.forEach((key) => {
                if (item[key]) {
                  const d = new Date(item[key]);
                  item[key] = isNaN(d.getTime()) ? item[key] : d;
                }
              });
            });
          };

          parseDates(historyData, [
            "start_date",
            "end_date",
            "created_at",
            "modified_at",
          ]);

          this.RateCodeHistoryList = historyData;

          this.TotalRecords =
            (historyData.length > 0 && historyData[0].TOTAL_RECORDS) ||
            historyData.length;
        } else {
          this.RateCodeHistoryList = [];
          this.TotalRecords = 0;
        }
      } catch (error) {
        console.error("FetchRateCodeHistory error:", error);
        this.RateCodeHistoryList = [];
        this.TotalRecords = 0;
        this.ShowAlert(this.$t("UnknownError"), "failure", this.$t("Alert"));
      } finally {
        this.isLoading = false;
      }
    },
    async GetPlanSubscriptionRateListing() {
      debugger;
      const vueObj = this;
      vueObj.PlanRateSubscriptionDeatil = [];

      const url = `planId=${vueObj.planId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;

      try {
        const response = await DataService.GetPlanSubscriptionRateListing(url);
        debugger;

        if (
          response &&
          response.data &&
          response.data.result &&
          response.data.result.data
        ) {
          const RatehistoryData = response.data.result.data || [];
          vueObj.PlanRateSubscriptionDeatil = RatehistoryData;

          //  Calculate total records safely
          vueObj.TotalRecords =
            (RatehistoryData.length > 0 && RatehistoryData[0].TOTAL_RECORDS) ||
            RatehistoryData.length;
        } else {
          vueObj.PlanRateSubscriptionDeatil = [];
          vueObj.TotalRecords = 0;
        }
      } catch (error) {
        console.error("Error fetching Plan Subscription Rate Listing:", error);
        vueObj.PlanRateSubscriptionDeatil = [];
        vueObj.TotalRecords = 0;
      }
    },
  },
};
</script>

